/* 5.2 Menus
--------------------------------------------------------------*/
/* Animation for moving the page 'out of the way' of the sidebars. */
.site {
  transition: 0.1s transform ease-in; }

/* Menu sidebar. */
.main-navigation {
  clear: both;
  display: block;
  float: left;
  width: 100%; }
  .main-navigation ul {
    font-size: 16px;
    font-size: 0.88889rem;
    list-style: none;
    margin: 0;
    padding-left: 0;
    font-family: Gotham, "Proxima Nova", "Museo Sans", Lato, Corbel, "Gill Sans", sans-serif;
    text-transform: uppercase; }
  .main-navigation li {
    padding: 14px 28px;
    padding: 0.77778rem 1.55556rem;
    position: relative; }
  .main-navigation a {
    display: block;
    text-decoration: none; }

/*
 * 1. Even though these are overlapped by the main content area, we still have
 *    to hide them until they're displayed so they don't peek out above or
 *    below the main content when the scrolling is rubber-banding.
 */
.main-navigation-wrapper, .main-search-wrapper {
  display: none;
  /* 1 */
  width: 15.55556rem;
  height: 100%;
  position: fixed;
  overflow-y: scroll;
  top: 0; }

.main-navigation-wrapper {
  left: 0; }

.main-search-wrapper {
  right: 0; }

/*
 * Sidebar toggle buttons.
 * 1. Overlay the main .site container.
 */
.menu-toggle, .search-toggle {
  font-size: 18px;
  font-size: 1rem;
  width: 224px;
  width: 12.44444rem;
  height: 84px;
  height: 4.66667rem;
  line-height: 84px;
  line-height: 4.66667rem;
  position: fixed;
  top: 0;
  z-index: 10;
  /* 1 */
  display: block;
  padding-top: 1.55556rem;
  border: 0;
  border-radius: 0;
  box-shadow: none;
  text-shadow: none;
  background: transparent;
  font-weight: bold;
  font-family: Gotham, "Proxima Nova", "Museo Sans", Lato, Corbel, "Gill Sans", sans-serif;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  /* Same transition duration as links. */ }
  .menu-toggle, .menu-toggle:hover, .search-toggle, .search-toggle:hover {
    transition: 0.2s color ease-in; }
  .menu-toggle:hover, .menu-toggle:focus, .search-toggle:hover, .search-toggle:focus {
    color: midnightblue; }
  .menu-toggle:hover, .menu-toggle:focus, .menu-toggle:active, .search-toggle:hover, .search-toggle:focus, .search-toggle:active {
    box-shadow: none;
    text-shadow: none; }

.menu-toggle {
  left: 84px;
  left: 4.66667rem;
  transform: rotate(90deg);
  transform-origin: left top 0;
  transition: 0.1s left ease-in, 0 opacity linear 0.1s;
  /* Delay on show, to wait for main page to slide back over. */ }

.search-toggle {
  right: 84px;
  right: 4.66667rem;
  transform: rotate(-90deg);
  transform-origin: right top 0;
  transition: 0.1s right ease-in, 0 opacity linear 0.1s;
  /* Delay on show, to wait for main page to slide back over. */ }

/* Menu area is open. */
.menu-left-toggled {
  /* Shift the page over. */
  /* Show the navigation elements. */
  /* Hide the Search toggle button, it's unusable until we close this menu. */ }
  .menu-left-toggled .site {
    position: relative;
    transform: translate3d(280px, 0, 0);
    transform: translate3d(15.55556rem, 0, 0); }
  .menu-left-toggled .menu-toggle {
    left: 364px;
    left: 20.22222rem; }
  .menu-left-toggled .main-navigation-wrapper {
    display: block; }
  .menu-left-toggled .search-toggle {
    opacity: 0;
    transition: none; }

/* Search area is open. */
.menu-right-toggled {
  /* Shift the page over. */
  /* Show the search elements. */
  /* Hide the Menu toggle button, it's unusable until we close this menu. */ }
  .menu-right-toggled .site {
    position: relative;
    transform: translate3d(-280px, 0, 0);
    transform: translate3d(-15.55556rem, 0, 0); }
  .menu-right-toggled .search-toggle {
    right: 364px;
    right: 20.22222rem; }
  .menu-right-toggled .main-search-wrapper {
    display: block; }
  .menu-right-toggled .menu-toggle {
    opacity: 0;
    transition: none; }

/* Admin bar is being displayed, so move position-fixed elements down. */
.admin-bar .main-navigation-wrapper, .admin-bar .main-search-wrapper, .admin-bar .menu-toggle, .admin-bar .search-toggle {
  top: 32px; }
  @media screen and (max-width: 782px) {
    .admin-bar .main-navigation-wrapper, .admin-bar .main-search-wrapper, .admin-bar .menu-toggle, .admin-bar .search-toggle {
